.contact {



  &__map {

  }

  &__text {
    padding: 0 40px;
    font-family: 'Open Sans', sans-serif;
  }

  &__club{
    font-family: 'Open Sans', sans-serif;
    line-height: normal;
    color: #2a2521;

    & .wt {
      color: #fff;
    }

    & .or {
      color: #fd7400;
    }

    & > h3 {
      margin: 0;
      font-size: 24px;
      font-weight: 400;

      & > span {
        font-weight: 800;
      }

    }

    & > p {
      position: relative;
      padding-left: 30px;
      font-size: 14px;
      line-height: 24px;
      font-weight: 400;

      & > span {
        font-weight: 700;
      }

      &:before {
        content: "";
        position: absolute;
        top: 5px;
        left: 0;
        background: url(../images/geopin.png) no-repeat;
        width: 24px;
        height: 24px;
      }

    }

    & .geopin_or {
      &:before {
        content: "";
        position: absolute;
        top: 5px;
        left: 0;
        background: url(../images/geopin_or.png) no-repeat;
        width: 24px;
        height: 24px;
      }
    }

  }

  &__attr {
    margin: 0;
    padding: 10px 0;
    font-size: 24px;
    font-weight: 400;
    color: #2a2521;
    line-height: 42px;

    & > span {
      font-weight: 800;
      color: #fd7400;
    }

  }

}
