.video {
  background: #0f0f0f;

  &__wrap {
    padding: 90px 10px;
  }

  &__box {
    position: relative;
    height: 373px;
    margin: 10px;
    vertical-align: bottom;
  }

  &__item {
    position: absolute;
    bottom: 0;
    border-radius: 3px;
    background: #fff;
    font-family: 'Open Sans', sans-serif;

    &_rd {
      background: #b31217;
    }

  }

  &__item-title {
    margin: 0;
    font-size: 15px;
    font-weight: 600;
    line-height: 24px;
    color: #2a2521;
    padding: 15px;

    &_wt {
      color: #fff;
    }

  }

  &__item-video {
    vertical-align: top;
    width: 100%;
    height: 230px;
  }

  &__best {
    padding: 20px;
    text-align: center;
    font-size: 22px;
    font-weight: 400;
    color: #fff;

    & span {
      position: relative;
      padding-left: 35px;

      &:before {
        content: "";
        position: absolute;
        top: -1px;
        left: -10px;
        background: url(../images/best.png) no-repeat;
        width: 32px;
        height: 32px;
      }

    }

  }

}
