.trainers {
  background: #0f0f0f;
  padding-bottom: 60px;

  &__box {
    padding: 0 40px;

    &_bdr {
      border-right: 1px solid rgba(255, 255, 255, .13);
    }

  }

  &__item {
    padding: 20px 0;
  }

  &__icon {
    vertical-align: top;
    width: 100%;
    border-radius: 100%;
  }

  &__text {
    padding: 0 20px;
    line-height: normal;
    color: #E0E0E0;

    & h4 {
      font-family: 'Roboto Slab', serif;
      font-size: 24px;
      font-weight: 300;
      margin: 0;
    }

    & p {
      font-family: 'Open Sans', sans-serif;
      font-size: 16px;
      line-height: 24px;
    }

    & .quote {
      font-family: 'Roboto Slab', serif;
      font-style: italic;
      font-weight: 300;
    }

  }

}
